body {
  background-color: #2b2929;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.gallery {
  background-color: #454443;
  flex-wrap: wrap;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  justify-content: space-around;
  gap: 20px;
  display: flex;
}

.photo-card {
  width: calc(20% - 20px);
  border: 1px solid #ddd;
  border-image: linear-gradient(#e89c21, rgba(0, 0, 0, 0)) 1 100%;
  border-radius: 8px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.info {
  background-color: #e8ebea;
  padding: 10px;
}

.info-item {
  margin: 0;
}

.load-more {
  color: #feffff;
  cursor: pointer;
  background-color: #71706e;
  border-radius: 5px;
  margin-top: 10px;
  margin-bottom: 20px;
  padding: 10px 20px;
  transition: background-color .3s;
}

.load-more:hover {
  color: #2e2f2f;
  background-color: #e89c21;
}

